.c_header {

  @media (min-width: 992px) {
    z-index: 999 !important;
    width: 54px !important;
  }

  .menu-toggler.sidebar-toggler {
    //display: none;
  }

  &.navbar .page-logo {
    background-color: transparent !important;
    border-bottom: 1px solid #3b444c;
    height: 60px;
    line-height: 60px;
    font-size: 0;
    position: relative;
    box-shadow: -3px 0 12px rgba(0, 0, 0, .8);

    .full-logo {

      position: absolute;
      left: 55px;
      top: 1px;

      img {
        width: 115px;
      }
    }

    .logo-default {
      margin: 0;
    }

    .sidebar-toggler {
      text-align: center;
      position: absolute;
      left: 9px;
      cursor: pointer;
    }

    @media (max-width: 991px) {

      background-color: transparent !important;
      border-bottom: none;
      height: 60px;
      line-height: 60px;
      font-size: 0;
      position: relative;
      box-shadow: none;

    }
  }
}

.page-bar {
  background: none;

  .page-bar_buttons {
    display: inline-block;
    @include float(right);
  }
  .page-breadcrumb {
    &.c_breadcrumb {
      li {
        a {
          font-size: 14px;
          color: #272c30;
        }
      }
    }
  }
}