// Components
@import "basic";
@import 'global/login.scss';
@import 'global/header.scss';
@import 'global/side-menu.scss';
@import 'pages/_conf_images';
@import "pages/_file_manager";

html {
  min-height: 100%;
}

.clear {
  clear: both;
}

.tab-pane {
  display: none;
  .tab-pane.active {
    display: block;
  }
}

// side bar logo
.page-header.navbar .page-logo .logo-default {
  margin-top: 9px;
  height: 52px;
  margin-left: 40px !important;
}

@include rtl {
  .mt-checkbox > span:after {
    transform: rotateY(180deg) rotateZ(-30deg);
  }

  .dropdown-menu.pull-right {
    right: 0;
    left: auto;
  }

  .dropdown > .dropdown-menu.pull-right:before, .dropdown-toggle > .dropdown-menu.pull-right:before, .btn-group > .dropdown-menu.pull-right:before {
    left: auto;
    right: 9px;
  }

  .dropdown > .dropdown-menu.pull-right:after, .dropdown-toggle > .dropdown-menu.pull-right:after, .btn-group > .dropdown-menu.pull-right:after {
    left: auto;
    right: 10px;
  }
}

// Custom information block
.custom-info-block {
  margin-top: 3px;
  margin-bottom: 10px;
  background: #EEF1F5;
  @include float(left);
  padding: 10px 0;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  margin-right:0px;
}

// list portfolio
.list-portfolio {
  padding: 0px 15px;

  .items{
    margin-bottom: 20px;
  }

  .item-block {
    background: #EEF1F5;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    height:300px;
    overflow-y: auto;
    overflow-x: hidden;

    .fixOverlayDiv {
      height: 300px;
      position: relative;
      text-align: center;

      img {
        position: absolute;
        transform: translate(0, -50%);
        top: 50%;
        display: block;
        margin: 0 auto;
      }
    }

    h3 {
      margin: 0;
    }

    .infoside {
      padding-top: 10px;
      padding-bottom: 20px;
    }
  }
}

.tabbable-line > .tab-content.padding_table {
  padding-right: 15px;
  padding-left: 15px;
}

.portlet.box > .portlet-body {
  padding: 15px !important;
}